import React, { useState } from "react";
import "./InfoForm.css";

const NewInfoForm = (props) => {
  return (
    <div className="info-form">
      <div className="info-form-actions">
        <button onClick={props.showForm}>
          Click to add new contractor
        </button>
      </div>
    </div>
  );
};

export default NewInfoForm;
