import React, { useState } from "react";
import Card from "./components/UI/Card";
import Info from "./components/Info/Info";
import Header from "./components/Header/Header";
import InfoForm from "./components/InfoForm/InfoForm";
import NewInfoForm from "./components/InfoForm/NewInfoForm";
import "./App.css";
import InfoHeader from "./components/Info/InfoHeader";
import Footer from "./components/Footer/Footer";

function App() {
  const contractorData = [
    {
      id: "CON-1",
      contractorName: "Ipeco Nig Ltd",
      project: "Constuction of 2 bedroom flat @ Jibi, Dei-dei",
      registrationDate: "2020-10-02",
      projectMilestones: "4-milestones",
      milestoneCompleted: "2-milestones",
      projectCost: 15000000,
      amountPaid: 7800000,
      amountDue: 44000,
    },

    {
      id: "CON-2",
      contractorName: "Kamdi Nig Ltd",
      project: "Constuction of 2 bedroom flat @ Jibi, Dei-dei",
      registrationDate: "2020-10-02",
      projectMilestones: "4-milestones",
      milestoneCompleted: "2-milestones",
      projectCost: 15000000,
      amountPaid: 7800000,
      amountDue: 44000,
    },
    {
      id: "CON-3",
      contractorName: "Skia Nig Ltd",
      project: "Constuction of 2 bedroom flat @ Jibi, Dei-dei",
      registrationDate: "2020-10-02",
      projectMilestones: "4-milestones",
      milestoneCompleted: "2-milestones",
      projectCost: 15000000,
      amountPaid: 7800000,
      amountDue: 44000,
    },
  ];

  const [contractorInfo, setContractorInfo] = useState(contractorData);
  const [dataFormDisplay, setDataFormDisplay] = useState(false);

  const contractorInfoHandler = (newAddedContractor) => {
    setContractorInfo((prevInfo) => {
      return [newAddedContractor, ...prevInfo];
    });
  };

  const showFormHandler = () => {
    setDataFormDisplay(true);
  };

  const hideFormHandler = () => {
    setDataFormDisplay(false);
  };

  return (
    <div>
      <Header />
      <Card>
        {!dataFormDisplay && <NewInfoForm showForm={showFormHandler} />}
        {dataFormDisplay && (
          <InfoForm
            onInfoFormSubmit={contractorInfoHandler}
            hideForm={hideFormHandler}
          />
        )}

        <div className="app">
          <InfoHeader />
          {contractorInfo.map((data) => (
            <Info
              key={data.contractorName}
              id={data.id}
              name={data.contractorName}
              description={data.project}
              date={data.registrationDate}
              cost={data.projectCost}
              totalMilestones={data.projectMilestones}
              completed={data.milestoneCompleted}
              paid={data.amountPaid}
              due={data.amountDue}
            />
          ))}
        </div>
      </Card>
      <Footer />
    </div>
  );
}

export default App;
