import React, { useState } from "react";

import "./InfoForm.css";

const InfoForm = (props) => {
  const [newContractor, setNewContractor] = useState({
    contractorName: "",
    project: "",
    registrationDate: "",
    projectMilestones: "",
    milestoneCompleted: "",
    projectCost: "",
    amountPaid: "",
    amountDue: "",
  });

  const inputNameHandler = (event) => {
    event.preventDefault();
    setNewContractor((prevInfo) => {
      return { ...prevInfo, contractorName: event.target.value };
    });
  };

  const inputProjectHandler = (event) => {
    event.preventDefault();
    setNewContractor((prevInfo) => {
      return { ...prevInfo, project: event.target.value };
    });
  };

  const inputDateHandler = (event) => {
    event.preventDefault();
    setNewContractor((prevInfo) => {
      return { ...prevInfo, registrationDate: event.target.value };
    });
  };

  const selectMilestoneHandler = (event) => {
    event.preventDefault();
    setNewContractor((prevInfo) => {
      return { ...prevInfo, projectMilestones: event.target.value };
    });
  };
  const selectCompletedMileHandler = (event) => {
    event.preventDefault();
    setNewContractor((prevInfo) => {
      return { ...prevInfo, milestoneCompleted: event.target.value };
    });
  };
  const inputCostHandler = (event) => {
    event.preventDefault();
    setNewContractor((prevInfo) => {
      return { ...prevInfo, projectCost: event.target.value };
    });
  };
  const inputPaidHandler = (event) => {
    event.preventDefault();
    setNewContractor((prevInfo) => {
      return { ...prevInfo, amountPaid: event.target.value };
    });
  };
  const inputDueHandler = (event) => {
    event.preventDefault();
    setNewContractor((prevInfo) => {
      return { ...prevInfo, amountDue: event.target.value };
    });
  };

  const infoFormSubmitHandler = (event) => {
    event.preventDefault();
    const newContractorData = {
      ...newContractor,
      id: "CON-" + Math.trunc(Math.random() * 3),
    };

    props.onInfoFormSubmit(newContractorData);

    setNewContractor({
      contractorName: "",
      project: "",
      registrationDate: "",
      projectMilestones: "",
      milestoneCompleted: "",
      projectCost: "",
      amountPaid: "",
      amountDue: "",
    });
  };

  return (
    <form className="info-form" onSubmit={infoFormSubmitHandler}>
      <div className="info-form-input">
        <div className="info-form-input__controls">
          <div className="info-form-input__control">
            <label>Name of Contractor</label>
            <input
              type="text"
              value={newContractor.contractorName}
              onChange={inputNameHandler}
            ></input>
          </div>
          <div className="info-form-input__control">
            <label>Project Description</label>
            <textarea
              value={newContractor.project}
              onChange={inputProjectHandler}
            ></textarea>
          </div>
        </div>

        <div className="info-form-input__controls">
          <div className="info-form-input__control">
            <label>Date of Registration</label>
            <input
              type="date"
              value={newContractor.registrationDate}
              onChange={inputDateHandler}
            ></input>
          </div>
          <div className="info-form-input__control">
            <label>Project milestone completed</label>
            <select
              value={newContractor.milestoneCompleted}
              onChange={selectCompletedMileHandler}
            >
              <option>Please select an option</option>
              <option>First milestone</option>
              <option>Second milestone</option>
              <option>Third milestone</option>
              <option>Fourth milestone</option>
              <option>Fifth milestone</option>
              <option>All milestones</option>
            </select>
          </div>
        </div>

        <div className="info-form-input__controls">
          <div className="info-form-input__control">
            <label>Total cost of project</label>
            <input
              type="number"
              min="100"
              value={newContractor.projectCost}
              onChange={inputCostHandler}
            ></input>
          </div>
          <div className="info-form-input__control">
            <label>Amount Paid</label>
            <input
              type="number"
              value={newContractor.amountPaid}
              min="100"
              onChange={inputPaidHandler}
            ></input>
          </div>
        </div>
        <div className="info-form-input__controls">
          <div className="info-form-input__control">
            <label>Project milestones</label>
            <select
              value={newContractor.projectMilestones}
              onChange={selectMilestoneHandler}
            >
              <option>Please select an option</option>
              <option>1</option>
              <option>2</option>
              <option>3</option>
              <option>4</option>
              <option>5</option>
              <option>6</option>
            </select>
          </div>
          <div className="info-form-input__control">
            <label>Amount Due</label>
            <input
              type="number"
              min="100"
              value={newContractor.amountDue}
              onChange={inputDueHandler}
            ></input>
          </div>
        </div>
      </div>
      <div className="info-form-actions">
        <button type="submit">Add New Contractor</button>
        <button type="cancel" onClick={props.hideForm}>
          Cancel
        </button>
      </div>
    </form>
  );
};

export default InfoForm;
